@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}



body {
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1 1;
}

.form-control-check input {
  flex: 2 1;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}


body {
    height: 100%;
}

.landing-page {
    display: flex;
    justify-content: space-evenly;
    position: absolute;
    margin: 30px;
    top: 100px;
    width: 100%;
}

.landing-page-flex {
    display: flex;
    justify-content: space-evenly;
}

.landing-page .landing-description {
    margin-top: auto;
    margin-bottom: auto;
    padding-bottom: 2rem;
    margin-right: 4rem;
}

.landing-page__header {
    font-size: 3.3rem;
    padding-bottom: 1.5rem;
}
.navbar-brand {
   margin-left: 16px;
}

.nav-link {
   margin-right: 16px;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 80%;
  height: 700px;
  min-height: 600px;
  align-self: center;
  margin: 2rem;
  background-color: white;

}

.main-div{
  margin:2rem ;

}

.container-fluid {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 100%;
  align-self: start;
  margin: 2rem;
}

.card {
  margin-bottom: 1em;
}
.card {
    width: 100%;
    margin-bottom: 1em;
}

.container {
    background-color: rgb(162, 159, 159);
}
